import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MainSharedService } from 'src/app/util/shared.service';
import * as moment from 'moment';
import { DeviceService } from 'src/app/core/services/device.service';
import { fromEvent } from 'rxjs';
import { tap, throttleTime } from 'rxjs/operators';
import { MovidaBrandsComponent } from 'projects/zkm-components/src/lib/components/movida-brands/movida-brands.component';
import { ShowcaseFindComponent } from 'projects/zkm-components/src/lib/components/movida-showcase/movida-showcase-find/showcase-find.component';

@Component({
  selector: 'home-scroll',
  templateUrl: './home-scroll.component.html',
  styleUrls: ['./home-scroll.component.scss']
})
export class HomeScrollComponent implements OnInit {

  @ViewChild(MovidaBrandsComponent) brandsComponent: MovidaBrandsComponent;
  @ViewChild(ShowcaseFindComponent) showcaseFind: ShowcaseFindComponent;

  // Variavel declarada por causa de lint na template

  public premiumMode : boolean = false;

  // Inicio Variaveis para as novas vitrines
  public isFirstTimeBrandsLoading: boolean = true;
  public isFirstVitrineLoadingMost: boolean = true;

  public canLoadBrands: boolean = false;
  public canLoadMostSearched: boolean = false;
  public canLoadPromotions: boolean = true;
  public canLoadAppDriver: boolean = false;
  public canLoadReasons: boolean = false;
  public canLoadBenefits: boolean = false;

  public skeletonItemsDay: Array<{}> = [{}, {}, {}];
  public skeletonItemsMost: Array<{}> = [{}, {}, {}, {}];
  public skeletonBrands: Array<{}> = [{}, {}, {}, {}, {}, {}, {}, {}];
  // Fim Variaveis para as novas vitrines

  constructor(
    private router: Router,
    private shared: MainSharedService,
    private deviceService: DeviceService,
  ) {
    moment.locale();
      shared.premiumModeChangeEmitted$.subscribe(text => {
      this.premiumMode = text;
    });
    if (!this.router.url.includes('/assinatura/detalhes')) {
      this.premiumMode = false;
    }
  }

  onScroll(event) {
    if (this.isFirstTimeBrandsLoading && window.pageYOffset > 180) {
      this.brandsComponent.startFetchBrands();
      this.isFirstTimeBrandsLoading = false;
    }
    if (this.isFirstVitrineLoadingMost && window.pageYOffset > 430) {
      this.showcaseFind.startFetchMostSearched();
      this.isFirstVitrineLoadingMost = false;

      // show final components of page
      this.canLoadAppDriver = true;
      this.canLoadReasons = true;
      this.canLoadBenefits = true;
    }
  }

  ngOnInit() {

    if (this.deviceService.isMobile()) {
      this.skeletonItemsDay = [{}, {}];
    }

    fromEvent(window, 'scroll').pipe(
      throttleTime(300),
      tap(event => this.onScroll(event))
    ).subscribe();
  }

  showCaseDayLoaded(loaded: boolean) {
    this.canLoadPromotions = false;
  }

  showCaseFindLoaded(loaded: boolean) {
    this.canLoadMostSearched = true;
  }

  brandsLoaded(loaded: boolean) {
    this.canLoadBrands = true;
  }
}
