//import { transition } from '@angular/animations';
import { Component, OnInit } from '@angular/core';
import Splide from '@splidejs/splide';

@Component({
  selector: 'app-motivos',
  templateUrl: './motivos.component.html',
  styleUrls: ['./motivos.component.scss']
})
export class MotivosComponent implements OnInit {

  splide : any = null;

  motivos : Array<Object> = [
    {
      image: 'assets/img/motivos/image_1.webp',
      title: 'Escolha o modelo ideal',
      text: 'O automóvel dos seus sonhos está aqui! Temos veículos econômicos, confortáveis, espaçosos e luxuosos. Veja todos os nossos modelos ou, se preferir, utilize os filtros para encontrar o carro perfeito para atender suas necessidades.',
      link: '/assinatura/busca',
      link_text: 'Conheça nossos modelos'
    },
    {
      image: 'assets/img/motivos/image_2.webp',
      title: 'Defina a franquia',
      text: 'Temos franquias de 1.000 km a 3.000 km. E é bom ressaltar que elas são cumulativas! Quer um exemplo? Num plano de 24 meses com franquia de 2.000 km, você pode rodar 48 mil km com seu carro até o fim do contrato. Sensacional, né?',
      link: '/assinatura/busca',
      link_text: 'Conheça nossos modelos'
    },
    {
      image: 'assets/img/motivos/image_3.webp',
      title: 'Selecione o prazo',
      text: 'Você pode ficar com o seu Movida Zero Km por um período mínimo de 12 meses. Mas fique tranquilo, pois também temos planos de 18, 24, 30, 36 e 48 meses. Dessa forma, você pode curtir o seu carro por muito mais tempo.',
      link: '/assinatura/busca',
      link_text: 'Conheça nossos modelos'
    },
    {
      image: 'assets/img/motivos/image_4.webp',
      title: 'Saia de automóvel Zero Km',
      text: 'Agora você está pronto para curtir seu Movida Zero Km! Como já sabemos que você não vai querer saber de outra vida, vale dizer que, a cada renovação de contrato, nossos clientes levam para casa um carro novinho em folha.',
      link: '/assinatura/busca',
      link_text: 'Conheça nossos modelos'
    }
  ];

  constructor() { }

  ngOnInit(): void {
    setTimeout(() => this.initializeSplideMobile(), 0);
    setTimeout(() => this.initializeSplideDesktop(), 0);
  }

  initializeSplideMobile() {
    new Splide('.splide_motivos_mobile', {
      focus: 'center',
      autoWidth: true,
      perMove: 1,
      flickPower: 50,
      flickMaxPages: 1,
      pagination: false,
      arrows: false,
    }).mount();

  }

  initializeSplideDesktop() {
    this.splide = new Splide('.splide_motivos_desktop',{
      type: 'fade',
      rewind: true,
      speed: 800,
      breakpoints: {
        800: {
          arrows: false
        }
      }
    }).mount();

    let pagination0List = document.getElementsByClassName('pagination-0');
    for (let i = 0; i < pagination0List.length; i++) {
      pagination0List[i].classList.add('is-active');
    }

    this.splide.on('move', function (newIndex, prevIndex) {
      let itensAdd = document.getElementsByClassName('pagination-' + newIndex);
      for (let i = 0; i < itensAdd.length; i++) {
        itensAdd[i]['classList'].add('is-active');
      }

      let itensRemove = document.getElementsByClassName('pagination-' + prevIndex);
      for (let i = 0; i < itensRemove.length; i++) {
        itensRemove[i]['classList'].remove('is-active');
      }
    });
  }
}
