import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location} from '@angular/common';
import { API_CHAORDIC_SEARCH } from 'src/app/core/api/app.api';
import { MainSharedService } from 'src/app/util/shared.service';
import { CarDetailService } from 'src/app/core/services/car-detail.service';
import { OffersService } from 'src/app/core/services/offers.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertMessageComponent } from 'src/app/shared/alert-message/alert-message.component';
import { CheckLoginService } from 'src/app/core/services/checklogin.service';
import { UserService } from 'src/app/core/services/user.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { LoginButtonComponent } from 'src/app/shared/login-button/login-button.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  providers: [AlertMessageComponent]
})

export class HomeComponent implements OnInit, OnDestroy {

  readonly disableRegionSelection: boolean = false;
  readonly enableToggleHeaderSearch: boolean = true;

  public apiSearch: string;
  public offers: any = new Array<any>();
  public vitrines: { data: [], title: string };
  public subscriptions: any = [];
  public user_region: any;
  public stateId: any;
  public stateFilter: any;
  public cityId: any;
  public cityFilter: any;
  public catalogCityId: any;
  public catalogStateId: any;

  out = '';

  constructor(
    private router: Router,
    private gtm: GtmService,
    private route: ActivatedRoute,
    private shared: MainSharedService,
    private offerService: OffersService,
    private carDetailService: CarDetailService,
    private checkLoginService: CheckLoginService,
    private userService: UserService,
    private login: LoginButtonComponent,
    private location: Location,
    //@Inject(APP_BASE_HREF) private baseHref:string
  ) { }

  ngOnInit() {
    this.checkLoginService.check()
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Movida - Zero Km');

    this.getCoupon();
    if(this.router.url.includes('em/') && this.shared.vendedorID === undefined) {
      this.linkpermanent();
    }

    if(this.router.url.includes('auth/')) {
      const token = this.route.snapshot.params.token;
      this.location.replaceState('/');
      this.login.getLogin(token);
    }

    this.apiSearch = API_CHAORDIC_SEARCH;
  }

  scrollTo() {
    window.scrollTo({
      top: 900,
      behavior: 'smooth'
    });
  }

  getCoupon() {
    this.route.queryParams.subscribe(params => {
      if (params.c) {
        /** CPN = cupom promocional */
        localStorage.setItem('CPN', params.c);
      }
    });
  }

  getProtectionsCoinsurance() {
    this.carDetailService.getProtectionCoinsurance().subscribe(res => {
      if (res !== null && res !== undefined && res && res.success) {
        localStorage.setItem('coinsurances', JSON.stringify(res.data));
      }
    })
  }

  getOffers(stateFilter, cityFilter) {
    this.shared.setCounterLoading(true);
    let subs = this.offerService.getOffers(stateFilter, cityFilter).subscribe(res => {
      if (!res || res === null || res === undefined || res.code === 3001 || res.message === 'Query not found') {
      } else {
        this.offers = res.products;
        this.offers.title = 'Desconto de 50% nas 3 primeiras parcelas';
      }
    }, ex => {
      this.shared.setCounterLoading(false);
    });
    this.subscriptions.push(subs);
  }

  reloadComponent() {
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate([currentUrl]);
  }

  async linkpermanent(){
    await this.userService.linkPermanent(this.route.snapshot.params.username);

    const url = new URL(window.location.href);
    const pathName = url.pathname.replace('/', '');
    const username = localStorage.getItem('username');
    const regex = `(${username})`;

    if(username || pathName.match(regex))
      document.location.reload();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach(subscriptions => subscriptions.unsubscribe());
  }
}
