import { Component, Input, Output, EventEmitter } from "@angular/core";
import { ShowCaseService } from "../showcase.service";
import * as moment from 'moment';
import { Car } from "../showcase.model"

@Component({
  selector: 'movida-showcase-find',
  templateUrl: './showcase-find.component.html',
  styleUrls: ['./showcase-find.component.scss']
})
export class ShowcaseFindComponent {

  @Input() type: string = 'find';
  @Output() contentLoaded: EventEmitter<boolean> = new EventEmitter<boolean>();

  public isLoadingMostSearched: boolean = true;

  constructor(
    private showcaseService: ShowCaseService,
  ) {
		moment.locale();
	}

  public vitrines = { data: [], title: '' };
  public carros: Array<Car> = [];

  startFetchMostSearched(): void {
    let pickape_today = moment().add(1, 'day').format('DD/MM/YYYY 23:59');
		let pickape_return = moment().add(1441, 'days').format('DD/MM/YYYY 23:59');
		this.getShowcase(pickape_today, pickape_return);
  }

	getShowcase(pickape_today, pickape_return) {
		this.showcaseService.getCarsMostSearched(pickape_today, pickape_return, "380", "380").subscribe(res => {
			if (res !== null || res !== undefined || res.success) {
        res.data.forEach((vehicle) => {
          this.carros.push({
            brand: vehicle.VehAvail.VehAvailCore.Vehicle.VehMakeModel.Name.split(' ')[0],
            model: vehicle.VehAvail.VehAvailCore.Vehicle.VehMakeModel.Name.split(' ')[1],
            detail: vehicle.VehAvail.VehAvailCore.Vehicle.Description,
            adds: vehicle.adds,
            promotionValue: vehicle.VehAvail.VehAvailCore.TotalCharge.Periods[0].valor_mensal_total,
            value: vehicle.VehAvail.VehAvailCore.TotalCharge.Periods[1] ? vehicle.VehAvail.VehAvailCore.TotalCharge.Periods[1].valor_mensal_total:0,
            image: vehicle.image_transparente,
            expiration: vehicle.expiration_date,
            fipe: vehicle.VehAvail.VehAvailCore.Vehicle.FipeCode,
            msgComPromo: vehicle.msg_com_promo
          })
        });

        this.contentLoaded.emit(true);
        this.isLoadingMostSearched = false;
			}
		}, ex => {
		});

	}
}
