import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { API_CHAORDIC_SEARCH } from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})
export class OffersService {

  constructor(private http: HttpClient) { }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || "Server Error");
  }

  getOffers(stateFilter, cityFilter): Observable<any> {
    const url = `?terms=promocao&${stateFilter}&${cityFilter}&applyURL=&sortBy=ascPrice`;
    return this.http.get<any>(API_CHAORDIC_SEARCH + url).pipe(tap(data => catchError(this.errorHandler)));
  }
}
