import { Component, Input, Output, EventEmitter } from "@angular/core";
import { ShowCaseService } from "../showcase.service";
import * as moment from 'moment';
import { Car } from "../showcase.model"
import { cars } from "./mock-cars";


@Component({
	selector: 'movida-showcase-day',
	templateUrl: './showcase-day.component.html',
	styleUrls: ['./showcase-day.component.scss']
})
export class ShowcaseDayComponent {

	@Input() type: string = 'day'
  @Output() contentLoaded: EventEmitter<boolean> = new EventEmitter<boolean>();

	constructor(private showcaseService: ShowCaseService) {
		moment.locale();
	}

	public vitrines = { data: [], title: '' };
	public isMobile = false;
	public subscriptions: any = [];
	public isLoadingVitrine: boolean = true;

	public carros: Array<Car> = cars;
	public titulo: any = ''
	public countDate: number = 0
	public countDown: any = 0

	ngOnInit() {
		let pickape_today = moment().add(1, 'day').format('DD/MM/YYYY 23:59')
		let pickape_return = moment().add(1441, 'days').format('DD/MM/YYYY 23:59')
		this.getShowcaseDay(pickape_today, pickape_return)
	}

	getShowcaseDay(pickape_today, pickape_return) {
		let subs = this.showcaseService.getCarsPromotionsDay(pickape_today, pickape_return, "380", "380").subscribe(res => {
    if (res !== null || res !== undefined || res.success) {

    const newCars = [];
    res.data.forEach((vehicle) => {
      if(vehicle.title){
        this.titulo = vehicle.title
      } else {
        newCars.push({
          brand: vehicle.VehMakeModel.split(' ')[0],
          model: vehicle.VehMakeModel.split(' ')[1],
          detail: vehicle.Description,
          adds: [
            vehicle.Adds[0],
            vehicle.Adds[1],
            vehicle.Adds[2],
            vehicle.Adds[3]],
          promotionValue: vehicle.Periods[0].valor_mensal_total,
          value: vehicle.Periods[1] ? vehicle.Periods[1].valor_mensal_total:1000,
          image: vehicle.image_transparente,
          expiration: this.getSecondsCountDown(vehicle.expiration_date),
          fipe: vehicle.Fipe,
          msgComPromo: vehicle.msg_com_promo
        })
      }
    });

		this.createCountDown();
    this.carros = newCars.slice(0, 3);

    this.isLoadingVitrine = false;
    this.contentLoaded.emit(true);
		this.verifySameValue(this.carros);
	}
}, ex => {
		});
		this.subscriptions.push(subs);
	}
	verifySameValue(cars) {
		cars.forEach((car) => {
			if(car.expiration == cars[0].expiration) {
				this.countDate += 1
			}
		})
	}

	getSecondsCountDown(time) {
		var dtvalidCountDown = (time + '23:59:59')
		var dtCount = moment(dtvalidCountDown,"DD/MM/YYYY HH:mm:ss").diff(new Date());
		var dtDuration = moment.duration(dtCount);
		var dtResult = Math.floor(dtDuration.asSeconds());
		return dtResult;
	}

  createCountDown() {
    const duration = moment.duration(this.carros[0].expiration, 's');
		const intervalId = setInterval(() => {
			duration.subtract(1, "s");
			const validHours = Math.floor(duration.asHours()) + '';
			const validMinutes = Math.floor(duration.minutes()) + '';
			const validSeconds = Math.floor(duration.seconds()) + '';
			var addZeroHours = '';
			var addZeroMinutes = '';
			var addZeroSeconds = '';
			if(validHours.length == 1){addZeroHours = '00'} else
			if(validHours.length == 2){addZeroHours = '0'} else {addZeroHours = ''}
			if(validMinutes.length == 1){addZeroMinutes = '0'} else {addZeroMinutes = ''}
			if(validSeconds.length == 1){addZeroSeconds = '0'} else {addZeroSeconds = ''}

			let totalHours = parseInt(addZeroHours+Math.floor(duration.asHours()));
			this.countDown = totalHours.toString().padStart(2, '0')+':'
			+addZeroMinutes+duration.minutes()+':'
			+addZeroSeconds+duration.seconds();

			if (duration.asSeconds() !== 0) return;
			clearInterval(intervalId);
		}, 1000);
  }
}
